import React, { useState } from 'react';
import * as XLSX from 'xlsx';
import './ExcelProcessor.css';

function ExcelProcessor() {
  const [excelSummaries, setExcelSummaries] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const CELL_RANGE = 'B25:N33'; // Hardcoded range

  const processExcelFile = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const data = e.target.result;
          const workbook = XLSX.read(data, { type: 'array' });
          
          // Process all sheets
          const sheetSummaries = workbook.SheetNames.map(sheetName => {
            const worksheet = workbook.Sheets[sheetName];
            
            // Get the data for the specified range
            const jsonData = XLSX.utils.sheet_to_json(worksheet, {
              range: CELL_RANGE,
              header: 1, // This will give us raw data with headers as first row
              defval: '', // Use empty string for empty cells
              raw: false // Convert all values to strings
            });

            // Get headers from first row or create empty headers if no data
            const headers = jsonData[0] || Array(13).fill(''); // 13 columns from B to N
            const dataRows = jsonData.slice(1);

            // Create summary for this sheet
            return {
              sheetName,
              range: CELL_RANGE,
              rowCount: dataRows.length,
              columnCount: headers.length,
              columns: headers,
              preview: dataRows.slice(0, 5) // First 5 rows as preview
            };
          });

          // Create overall file summary
          const summary = {
            fileName: file.name,
            sheets: sheetSummaries
          };

          resolve(summary);
        } catch (error) {
          console.error('Error processing file:', error);
          reject(error);
        }
      };
      reader.onerror = (error) => reject(error);
      reader.readAsArrayBuffer(file);
    });
  };

  const handleFileUpload = async (event) => {
    const files = event.target.files;
    if (!files.length) return;

    setIsLoading(true);
    try {
      const summaries = await Promise.all(
        Array.from(files).map(file => processExcelFile(file))
      );
      setExcelSummaries(summaries);
    } catch (error) {
      console.error('Error processing files:', error);
      alert('Error processing files. Please make sure they are valid Excel files.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="excel-processor">
      <h1>Excel File Processor</h1>
      <div className="upload-section">
        <input
          type="file"
          multiple
          accept=".xlsx,.xls"
          onChange={handleFileUpload}
          className="file-input"
        />
      </div>

      {isLoading && (
        <div className="loading">
          Processing files...
        </div>
      )}

      <div className="summaries-container">
        {excelSummaries.map((summary, fileIndex) => (
          <div key={fileIndex} className="summary-card">
            <h2>{summary.fileName}</h2>
            {summary.sheets.map((sheet, sheetIndex) => (
              <div key={sheetIndex} className="sheet-summary">
                <h3>Sheet: {sheet.sheetName}</h3>
                <div className="summary-details">
                  <p><strong>Range:</strong> {sheet.range}</p>
                  <p><strong>Rows:</strong> {sheet.rowCount}</p>
                  <p><strong>Columns:</strong> {sheet.columnCount}</p>
                  <p><strong>Column Names:</strong> {sheet.columns.join(', ')}</p>
                </div>
                <div className="preview-section">
                  <h4>Preview (First 5 rows)</h4>
                  <div className="preview-table">
                    <table>
                      <thead>
                        <tr>
                          {sheet.columns.map((col, colIndex) => (
                            <th key={colIndex}>{col}</th>
                          ))}
                        </tr>
                      </thead>
                      <tbody>
                        {sheet.preview.map((row, rowIndex) => (
                          <tr key={rowIndex}>
                            {row.map((cell, cellIndex) => (
                              <td key={cellIndex}>{cell}</td>
                            ))}
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
}

export default ExcelProcessor; 